* {
  font-family: 'Roboto', 'sans-serif';
  margin: 0;
  padding: 0;
}

html {
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    background: rgb(221, 220, 220);
}

/* #id {
    display: flex;
    justify-content: center;
    align-items: center;
} */
.calculator {
    width: 400px;
    border: 1px dashed green;
    text-align: right;
    background: #6d6d6d;
    padding: 2px;
}

.clear-container {
    display: flex;
}

.clear {
    height: 20px;
    width: 100%;
}

button {
    border: none;
    box-shadow: none;
    margin: 2px;
    min-height: 60px;
    max-height: 80px;
    font-size: 1em;
}

.numpad-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.buttons-container {
    display: grid;
    grid-template-columns: 4fr 1fr;
}
.operations-container {
    display: flex;
    flex-direction: column;
}
.operations-container button, .clear, #\., #equals {
    font-family: inherit;
    background: #424242;
    color: white;
}
.number {
    background: #1b1b1b;
    color: #fff;
}
.display {
    padding: 10px;
    font-size: 38px;
    min-height: 50px;
    color: #fff;
    word-break: break-all;
}
.memory {
    padding: 10px;
    min-height: 40px;
    font-size: 16px;
    color: rgba(255, 255, 255, 0.6);
}
